<template>
    <div>    ...</div>
</template>

<script>
import Vue from "vue";
export default {
    name: "Welcome",
    data() {
        return {};
    },
    mounted() {
        var curTime = new Date();
        if (window.sessionStorage.TokenExpire) {
            var expiretime = new Date(
                Date.parse(window.sessionStorage.TokenExpire)
            );
            if (curTime >= expiretime) {
                this.$router.push("/login");
            }
        } else {
            this.$router.push("/login");
        }
    },
};
</script>

<style scoped>
</style>


