import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from "vuex-persistedstate";
import Cookies from 'js-cookie'
import dict from "./assets/dic/dic.json";//json
import { dictionarys, getAllSites, getGroupAll } from "./api/api.js"
Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        token: null,
        tokenExpire: null,
        tagsStoreList: [],
        language: Cookies.get('language') || 'en',
        //
        options: dict
    },
    getters: {
        tagsStoreList: state => state.tagsStoreList
    },
    plugins: [
        createPersistedState({
            storage: window.sessionStorage,
            reducer(val) {
                return {
                    // state
                    options: val.options
                };
            }
        })
    ],
    mutations: {
        saveToken(state, data) {
            state.token = data;
            window.sessionStorage.setItem("Token", data);
        },
        saveTokenExpire(state, data) {
            state.tokenExpire = data;
            window.sessionStorage.setItem("TokenExpire", data);
        },
        saveTagsData(state, data) {
            state.tagsStoreList = data;
            sessionStorage.setItem("Tags", data)
        },
        SET_LANGUAGE: (state, language) => {
            state.language = language
            Cookies.set('language', language)
        },
        setOptions(state, obj) {
            let { key, value } = obj;
            try {
                state.options[key] = value;
            } catch (error) {
                console.log(error);
            }
        }
    },
    actions: {
        setLanguage({ commit }, language) {
            commit('SET_LANGUAGE', language)
        },
        /**
                * actionstorecontext
                 *   context:{    //{Object} store   Vuex  store  mutation
                 *       state,   store.$state
                 *       rootState,   store.$state,
                 *       commit,   store.$commit,mutations
                 *       dispatch,   store.$dispatch,dispatch
                 *       getters   store.$getters
                 *    }
                *  args   // Array keys
                *  new Promise() , ,resolve,reject=> {     } ;
                *  Promise
                * resolve = ;reject  = 
                */
        getOptions(context, args) {
            return new Promise((resolve, reject) => {
                let dict = context.state.options;//JSON
                let params = []
                if (args && args.length > 0) {
                    params = args.reduce((o, v, i) => {
                        if (dict[v] && dict[v].length == 0) {   //
                            o.push(v);
                        }
                        return o;
                    }, []);
                } else {
                    for (var d in dict) {
                        if (d == "Sites" || d == "Groups") {

                        } else {
                            params.push(d)
                        }
                    }
                }
                if (params.length === 0) {    //
                    resolve(true);
                    return false;
                }
                dictionarys()//vuex
                    .then(
                        res => {
                            if (res.success) {
                                params.map(v => {
                                    context.commit("setOptions", {
                                        key: v,
                                        value: res.data.filter((a) => { return a.datatype == v }) || []
                                    });
                                });
                                resolve(true, res);//
                            } else {

                            }

                        },
                        err => {
                            reject(err);
                        }
                    )
                    .catch(err => {
                        reject(err);
                    }
                    )
                getAllSites()//vuex
                    .then(
                        res => {
                            if (res.success) {
                                context.commit("setOptions", {
                                    key: "Sites",
                                    value: res.data || []
                                });
                                resolve(true, res);//
                            } else {

                            }

                        },
                        err => {
                            reject(err);
                        }
                    )
                    .catch(err => {
                        reject(err);
                    }
                    )
                getGroupAll()//vuex
                    .then(
                        res => {
                            if (res.success) {
                                context.commit("setOptions", {
                                    key: "Groups",
                                    value: res.data || []
                                });
                                resolve(true, res);//
                            } else {

                            }

                        },
                        err => {
                            reject(err);
                        }
                    )
                    .catch(err => {
                        reject(err);
                    }
                    )
            });
        }
    }
})



