import Vue from 'vue'
import Router from 'vue-router'
import Login from '../views/Login.vue'
import Welcome from '../views/Welcome'
import NoPage from '../views/404'

import Layout from "../views/Layout/Layout";
const _import = require('@/router/_import_' + process.env.NODE_ENV)//

Vue.use(Router)

const createRouter = () => new Router({
    mode: 'hash',
    base: 'index.html',
    routes: [
        {
            path: '/login',
            component: Login,
            name: 'login',
            iconCls: 'fa-address-card',//class
            meta: {
                title: '',
                NoTabPage: true,
                NoNeedHome: true // home
            },
            hidden: true
        },
        {
            path: '/',
            component: Welcome,
            name: '',
            iconCls: 'fa-qq',//class
            // hidden: true,
            meta: {
                title: '',
                requireAuth: true // 
            }
        },
        {
            path: '/404', component: NoPage, name: 'NoPage',
            meta: {
                title: 'NoPage',
                requireAuth: false,
                NoTabPage: true,
                NoNeedHome: true // home
            },
            hidden: true
        },
        {
            path: '*',
            hidden: true,
            redirect: { path: '/404' }
        }
    ]
})

const router = createRouter()

export function filterAsyncRouter(asyncRouterMap) {
    // asyncRouterMap 
    const accessedRouters = asyncRouterMap.filter(route => {
        if (route.path && !route.IsButton) {
            if (route.path === '/' || route.path === '-') {//Layout
                route.component = Layout
            } else {
                try {
                    route.component = _import(route.path.replace('/:id', ''))
                } catch (e) {
                    try {
                        route.component = () => import('@/views' + route.path.replace('/:id', '') + '.vue');
                    } catch (error) {
                        console.info('%c  ' + route.path.replace('/:id', '') + '.vue !', "color:red")
                    }
                }
            }
        }
        if (route.children && route.children.length && !route.IsButton) {
            route.children = filterAsyncRouter(route.children)
        }
        return true
    })

    return accessedRouters
}

export function resetRouter() {
    const newRouter = createRouter()
    router.matcher = newRouter.matcher // the relevant part
}


router.$addRoutes = (params) => {

    var f = item => {

        if (item['children']) {
            item['children'] = item['children'].filter(f);
            return true;
        } else if (item['IsButton']) {
            return item['IsButton'] === false;
        } else {
            return true;
        }

    }

    var params = params.filter(f);

    router.addRoutes(params)
}

export default router;



