import axios from 'axios';
import router from '../router/index'
import store from "../store";
import Vue from 'vue';

//let base = `http://test.admin.xhs-sz.com:9035`;
//let base = 'http://localhost:44351'http://test.admin.xhs-sz.com:9035;
// IIS IIS  CORS 
let base = process.env.NODE_ENV == "production" ? `http://${window.location.hostname}:9035` : 'http://test.admin.xhs-sz.com:9035';

// 
axios.defaults.timeout = 20000

var storeTemp = store;
axios.interceptors.request.use(
    config => {
        var curTime = new Date()
        var expiretime = new Date(Date.parse(storeTemp.state.tokenExpire))

        if (storeTemp.state.token && (curTime < expiretime && storeTemp.state.tokenExpire)) {
            // tokenhttp headertoken
            config.headers.Authorization = "Bearer " + storeTemp.state.token;
        }

        saveRefreshtime();
        //IE 
        if (config.url.indexOf('?') > -1) {
            config.url = config.url + `&n=${encodeURIComponent(Math.random())}`
        }
        else {
            config.url = config.url + `?n=${encodeURIComponent(Math.random())}`
        }
        return config;
    },
    err => {
        return Promise.reject(err);
    }
);

// http response 
axios.interceptors.response.use(
    response => {
        return response;
    },
    error => {
        // 
        var originalRequest = error.config;
        if (error.code == 'ECONNABORTED' && error.message.indexOf('timeout') != -1 && !originalRequest._retry) {

            Vue.prototype.$message({
                message: '',
                type: 'error'
            });

            originalRequest._retry = true
            return null;
        }

        if (error.response) {
            if (error.response.status == 401) {
                var curTime = new Date()
                var refreshtime = new Date(Date.parse(window.sessionStorage.refreshtime))
                // 
                if (window.sessionStorage.refreshtime && (curTime <= refreshtime)) {
                    return refreshToken({ token: window.sessionStorage.Token }).then((res) => {
                        if (res.success) {
                            Vue.prototype.$message({
                                message: 'refreshToken success! loading data...',
                                type: 'success'
                            });

                            store.commit("saveToken", res.response.token);

                            var curTime = new Date();
                            var expiredate = new Date(curTime.setSeconds(curTime.getSeconds() + res.response.expires_in));
                            store.commit("saveTokenExpire", expiredate);

                            error.config.__isRetryRequest = true;
                            error.config.headers.Authorization = 'Bearer ' + res.response.token;
                            return axios(error.config);
                        } else {
                            // token token
                            ToLogin()
                        }
                    });
                } else {
                    //  401 token
                    ToLogin()
                }

            }
            // 403 
            if (error.response.status == 403) {
                Vue.prototype.$message({
                    message: '',
                    type: 'error'
                });
                return null;
            }
            // 429 ip
            if (error.response.status == 429) {
                Vue.prototype.$message({
                    message: '',
                    type: 'error'
                });
                return null;
            }
            return error.response;
        }
        return ""; // 
    }
);


export const BaseApiUrl = base;

// 
export const requestLogin = params => {
    return axios.post(`${base}/api/login/Login`, params).then(res => res.data);
};

export const refreshToken = params => {
    return axios.get(`${base}/api/login/RefreshToken`, { params: params }).then(res => res.data);
};

export const saveRefreshtime = params => {

    let nowtime = new Date();
    let lastRefreshtime = window.sessionStorage.refreshtime ? new Date(window.sessionStorage.refreshtime) : new Date(-1);
    let expiretime = new Date(Date.parse(window.sessionStorage.TokenExpire))

    let refreshCount = 1;//
    if (lastRefreshtime >= nowtime) {
        lastRefreshtime = nowtime > expiretime ? nowtime : expiretime;
        lastRefreshtime.setMinutes(lastRefreshtime.getMinutes() + refreshCount);
        window.sessionStorage.refreshtime = lastRefreshtime;
    } else {
        window.sessionStorage.refreshtime = new Date(-1);
    }
};
const ToLogin = params => {

    store.commit("saveToken", "");
    store.commit("saveTokenExpire", "");
    store.commit("saveTagsData", "");
    window.sessionStorage.removeItem('user');
    window.sessionStorage.removeItem('NavigationBar');


    router.replace({
        path: "/login",
        query: { redirect: router.currentRoute.fullPath }
    });

    window.location.reload()

};

