    <div id="app">
        <transition v-if="!$route.meta.NoNeedHome" name="fade" mode="out-in">
            <el-row class="container">
                <el-col :span="24" class="header">
                    <el-col
                        :span="10"
                        class="logo collapsedLogo"
                        :class="
                            collapsed ? 'logo-collapse-width' : 'logo-width'
                        "
                    >
                        <div @click="toindex">
                            {{ collapsed ? sysNameShort : sysName }}
                        </div>
                    </el-col>
                    <el-col :span="10" class="logoban">
                        <div
                            :class="collapsed ? 'tools collapsed' : 'tools'"
                            @click="collapse"
                        >
                            <i class="fa fa-align-justify"></i>
                        </div>

                        <el-breadcrumb
                            separator="/"
                            class="breadcrumb-inner collapsedLogo"
                        >
                            <el-breadcrumb-item
                                v-for="item in $route.matched"
                                :key="item.path"
                            >
                                <span style="color: rgba(255, 255, 255, 0.7)">
                                    {{ item.name }}</span
                                >
                            </el-breadcrumb-item>
                        </el-breadcrumb>
                    </el-col>
                    <el-col :span="4" class="userinfo">
                        <el-dropdown trigger="hover">
                            <span class="el-dropdown-link userinfo-inner">
                                {{ sysUserName }}
                                <img
                                    src="./assets/logo.png"
                                    height="128"
                                    width="128"
                                />
                            </span>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item @click.native="Setting"
                                    ></el-dropdown-item
                                >
                                <el-dropdown-item divided @click.native="logout"
                                    ></el-dropdown-item
                                >
                            </el-dropdown-menu>
                        </el-dropdown>
                    </el-col>
                </el-col>
                <el-col :span="24" class="main">
                    <aside :class="collapsedClass">
                        <el-scrollbar
                            style="height: 100%; background: #2f3e52"
                            class="scrollbar-handle"
                        >
                            <el-menu
                                :default-active="$route.path"
                                class="el-menu-vertical-demo"
                                @open="handleopen"
                                @close="handleclose"
                                @select="handleselect"
                                unique-opened
                                router
                                :collapse="isCollapse"
                                background-color="#2f3e52"
                                style="border-right: none"
                                text-color="#fff"
                                active-text-color="#ffd04b"
                            >
                                <sidebar
                                    v-for="(menu, index) in routes"
                                    @collaFa="collapseFa"
                                    :key="index"
                                    :item="menu"
                                />
                            </el-menu>
                        </el-scrollbar>
                    </aside>

                    <el-col
                        :span="24"
                        class="content-wrapper"
                        :class="
                            collapsed ? 'content-collapsed' : 'content-expanded'
                        "
                    >
                        <div class="tags" v-if="showTags">
                            <div
                                id="tags-view-container"
                                class="tags-view-container"
                            >
                                <scroll-pane
                                    ref="scrollPane"
                                    class="tags-view-wrapper"
                                >
                                    <router-link
                                        v-for="(tag, index) in tagsList"
                                        ref="tag"
                                        :key="tag.path"
                                        :class="{ active: isActive(tag.path) }"
                                        :to="{
                                            path: tag.path,
                                            query: tag.query,
                                            fullPath: tag.fullPath,
                                        }"
                                        tag="span"
                                        @click.middle.native="closeTags(index)"
                                        class="tags-view-item"
                                    >
                                        {{ tag.title }}
                                        <span
                                            class="el-icon-close"
                                            @click.prevent.stop="
                                                closeTags(index)
                                            "
                                        />
                                    </router-link>
                                </scroll-pane>
                            </div>
                            <!--  -->
                            <div class="tags-close-box">
                                <el-dropdown @command="handleTags">
                                    <el-button size="mini">
                                        <i
                                            class="
                                                el-icon-arrow-down
                                                el-icon--right
                                            "
                                        ></i>
                                    </el-button>
                                    <el-dropdown-menu
                                        size="small"
                                        slot="dropdown"
                                    >
                                        <el-dropdown-item command="other"
                                            ></el-dropdown-item
                                        >
                                        <el-dropdown-item command="all"
                                            ></el-dropdown-item
                                        >
                                    </el-dropdown-menu>
                                </el-dropdown>
                            </div>
                        </div>
                        <transition name="fade" mode="out-in">
                            <div class="content-az">
                                <router-view></router-view>
                            </div>
                        </transition>
                    </el-col>
                </el-col>
            </el-row>
        </transition>

        <transition v-else name="fade" mode="out-in">
            <div class="content-az">
                <router-view></router-view>
            </div>
        </transition>
        <div
            class="v-modal"
            @click="collapse"
            v-show="SidebarVisible"
            tabindex="0"
            style="z-index: 2917"
        ></div>
    </div>
</template>
<script>
import Sidebar from "./components/Sidebar";
import ScrollPane from "./components/ScrollPane";
import { getUserByToken } from "./api/api";

export default {
    components: { Sidebar, ScrollPane },
    data() {
        return {
            sysName: "",
            sysNameShort: "AD",
            SidebarVisible: false,
            collapsed: true,
            zModalShadow: false,
            collapsedClass: "menu-expanded",
            ispc: false,
            sysUserName: "",
            sysUserAvatar: "",
            isCollapse: true,
            tagsList: [],
            form: {
                name: "",
                region: "",
                date1: "",
                date2: "",
                delivery: false,
                type: [],
                resource: "",
                desc: "",
            },
            routes: [],
            visible: false,
            top: 0,
            left: 0,
            selectedTag: {},
            affixTags: [],
        };
    },
    methods: {
        gotappath(Path) {
            // console.log(Path);
            this.$router.replace({
                path: Path,
            });
        },
        handleOpen(key, keyPath) {
            // console.log(key, keyPath);
        },
        toindex() {
            this.$router.replace({
                path: "/",
            });
        },
        handleClose(key, keyPath) {
            // console.log(key, keyPath);
        },
        onSubmit() {
            // console.log("submit!");
        },
        handleopen() {
            //console.log('handleopen');
        },
        handleclose() {
            //console.log('handleclose');
        },
        handleselect: function (a, b) {},
        //
        logout: function () {
            var _this = this;
            this.$confirm("?", "", {
                //type: 'warning'
            })
                .then(() => {
                    window.sessionStorage.clear();

                    global.antRouter = [];

                    this.tagsList = [];
                    this.routes = [];
                    this.$store.commit("saveTagsData", "");

                    _this.$router.push("/login");
                    window.location.reload();
                })
                .catch(() => {});
        },
        //
        Setting: function () {
            this.$message("");
            // var _this = this;
            // _this.$router.push("/System/My");
        },
        //
        collapse: function () {
            this.collapsed = !this.collapsed;
            if (this.collapsed) {
                this.collapsedClass = "menu-collapsed";
            } else {
                this.collapsedClass = "menu-expanded";
            }
            this.isCollapse = !this.isCollapse;
        },
        collapseFa: function () {
            if (!this.ispc) {
                this.collapse();
            }
        },
        showMenu(i, status) {
            this.$refs.menuCollapsed.getElementsByClassName(
                "submenu-hook-" + i
            )[0].style.display = status ? "block" : "none";
        },
        isActive(path) {
            return path === this.$route.fullPath;
        },
        // 
        closeTags(index) {
            const delItem = this.tagsList.splice(index, 1)[0];
            const item = this.tagsList[index]
                ? this.tagsList[index]
                : this.tagsList[index - 1];
            if (item) {
                delItem.path === this.$route.fullPath &&
                    this.$router.push(item.path);

                this.$store.commit(
                    "saveTagsData",
                    JSON.stringify(this.tagsList)
                );
            } else {
                this.$router.push("/");
            }
        },
        // 
        closeAll() {
            this.tagsList = [];
            this.$router.push("/");
            sessionStorage.removeItem("Tags");
        },
        // 
        closeOther() {
            const curItem = this.tagsList.filter((item) => {
                return item.path === this.$route.fullPath;
            });
            this.tagsList = curItem;

            sessionStorage.setItem("Tags", JSON.stringify(this.tagsList));
        },
        // 
        setTags(route) {
            if (!route.meta.NoTabPage) {
                const isExist = this.tagsList.some((item) => {
                    return item.path === route.fullPath;
                });
                !isExist &&
                    this.tagsList.push({
                        title: route.meta.title,
                        path: route.fullPath,
                        // name: route.matched[1].components.default.name
                    });
            }
        },
        // 
        handleTags(command) {
            command === "other" ? this.closeOther() : this.closeAll();
        },
        getUserInfoByToken(token) {
            var _this = this;
            var loginParams = { token: token };
            getUserByToken(loginParams).then((data) => {
                this.logining = false;
                if (!data.success) {
                    _this.$message({
                        message: data.message,
                        type: "error",
                    });
                } else {
                    _this.$notify({
                        type: "success",
                        message: `${data.response.uRealName} `,
                        duration: 3000,
                    });

                    _this.sysUserName = data.response.uRealName;
                    window.sessionStorage.user = JSON.stringify(data.response);
                }
            });
        },
    },
    mounted() {
        // console.log(this.$route);

        var tags = sessionStorage.getItem("Tags")
            ? JSON.parse(sessionStorage.getItem("Tags"))
            : [];

        if (tags && tags.length > 0) {
            this.tagsList = tags;
        }

        var NavigationBar = JSON.parse(
            window.sessionStorage.router ? window.sessionStorage.router : null
        );
        // var NavigationBar = global.antRouter;

        if (
            this.routes.length <= 0 &&
            NavigationBar &&
            NavigationBar.length >= 0
        ) {
            this.routes = NavigationBar;
        }

        // 
        this.collapse();
    },
    updated() {
        var user = JSON.parse(
            window.sessionStorage.user ? window.sessionStorage.user : null
        );
        if (user) {
            this.sysUserName = user.UserName;
            this.sysUserAvatar = user.avatar || "../assets/user.png";
        }

        var NavigationBar = JSON.parse(
            window.sessionStorage.router ? window.sessionStorage.router : null
        );

        if (NavigationBar && NavigationBar.length >= 0) {
            if (
                this.routes.length <= 0 ||
                JSON.stringify(this.routes) != JSON.stringify(NavigationBar)
            ) {
                this.routes = NavigationBar;
            }
        }
    },
    computed: {
        showTags() {
            if (this.tagsList.length > 1) {
                this.$store.commit(
                    "saveTagsData",
                    JSON.stringify(this.tagsList)
                );
            }
            return this.tagsList.length > 0;
        },
    },
    watch: {
        // routerroutertags
        $route: async function (newValue, from) {
            this.setTags(newValue);
            const tags = this.$refs.tag;
            this.$nextTick(() => {
                if (tags) {
                    for (const tag of tags) {
                        if (tag.to.path === this.$route.path) {
                            this.$refs.scrollPane.moveToTarget(tag, tags);
                            // when query is different then update
                            // if (tag.to.fullPath !== this.$route.fullPath) {
                            //     this.$store.dispatch('tagsView/updateVisitedView', this.$route)
                            // }
                            break;
                        }
                    }
                }
            });
        },
    },
    created() {
        // tag
        this.setTags(this.$route);
        this.ispc = window.screen.width > 680;

        if (this.ispc) {
            this.collapsedClass = "menu-expanded";
        } else {
            this.collapsedClass = "menu-expanded-mobile mobile-ex";
            this.collapsed = true;
            this.collapse();
        }
    },
};
</script>

