import router from '@/router'
import { resetRouter, filterAsyncRouter } from '@/router/index'

import { getNavigationBar, saveRefreshtime } from '@/api/api';
import store from "@/store";

//
var getRouter
if (!getRouter) {//
    if (!getObjArr('router')) {
        //
        var user = window.sessionStorage.user ? JSON.parse(window.sessionStorage.user) : null;
        if (user && user.uID > 0) {
            console.info(user.uID)
            var loginParams = { uid: user.uID };
            getNavigationBar(loginParams).then(data => {
                if (data.success) {
                    console.info('%c get navigation bar from api succeed!', "color:red")
                    getRouter = data.response.children//
                    saveObjArr('router', getRouter) //sessionStorage
                    // routerGo(to, next)//
                }
            });
        }
    } else {
        //sessionStorage
        console.info('%c get navigation bar from sessionStorage succeed!', "color:green")
        getRouter = getObjArr('router')//
        getRouter = filterAsyncRouter(getRouter) //
        router.$addRoutes(getRouter) //
        global.antRouter = getRouter //
    }

}

var storeTemp = store;
router.beforeEach((to, from, next) => {
    //Token
    {
        if (!storeTemp.state.token) {
            storeTemp.commit("saveToken", window.sessionStorage.Token)
        }
        if (!storeTemp.state.tokenExpire) {
            storeTemp.commit("saveTokenExpire", window.sessionStorage.TokenExpire)
        }
        saveRefreshtime();
        if (to.meta.requireAuth) {
            // 
            var curTime = new Date()
            var expiretime = new Date(Date.parse(window.sessionStorage.TokenExpire))
            if (storeTemp.state.token && storeTemp.state.token != "undefined") {
                // vuex statetoken
                next();
            } else {
                store.commit("saveToken", "");
                store.commit("saveTokenExpire", "");
                store.commit("saveTagsData", "");
                window.sessionStorage.removeItem('user');
                window.sessionStorage.removeItem('NavigationBar');
                window.sessionStorage.removeItem('router');

                next({
                    path: "/login",
                    query: { redirect: to.fullPath } // path
                });

                window.location.reload()


            }
        } else {
            next();
        }
    }

    //
    {
        //
        if (!getRouter) {
            if (!getObjArr('router')) {
                var user = window.sessionStorage.user ? JSON.parse(window.sessionStorage.user) : null;
                if (user && user.uID > 0) {
                    var loginParams = { uid: user.uID };
                    getNavigationBar(loginParams).then(data => {
                        console.log('router before each get navigation bar from api succeed!')
                        if (data.success) {
                            getRouter = data.response.children//
                            saveObjArr('router', getRouter) //sessionStorage
                            routerGo(to, next)//
                        }
                    });
                }
            } else {
                //sessionStorage
                getRouter = getObjArr('router')//
                routerGo(to, next)
            }
        } else {

            if (to.name && to.name != 'login') {
                getRouter = getObjArr('router')//
                global.antRouter = getRouter
                // routerGo(to, next)//
            }
            next()

        }
    }
});


function routerGo(to, next) {

    //
    getRouter = filterAsyncRouter(getRouter)
    resetRouter()

    //
    router.$addRoutes(getRouter)

    //
    global.antRouter = getRouter
    next({ ...to, replace: true })
}

//sessionStorage 
function saveObjArr(name, data) {
    sessionStorage.setItem(name, JSON.stringify(data))
}

//sessionStorage 
function getObjArr(name) {
    return JSON.parse(window.sessionStorage.getItem(name));
}


var buttonList = [];


export const getButtonList = (routePath, routers) => {
    routers.forEach(element => {
        if (routePath && element.path) {
            let path = routePath.toLowerCase();
            if (element.path && element.path.toLowerCase() === path) {
                buttonList = element.children || [];
                return;
            } else if (element.children) {
                getButtonList(path, element.children);
            }
        }
    });
    return buttonList;
};





